}

#back-home a 
{
    font-style: italic;
    color:#d4710f;
}

a 
{
    text-decoration: none;
}


@media screen and (max-width:930px)
{
  .About .text-container
  {
    width:75%;
  }

  .text-container h3 
  {
      font-size:38px;
  }
}

